* {
  padding: 0;
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.encabezado {
  display: flex;
  height: 25vh;
  background-color: black;
  color: white;
  justify-content: space-between;
  align-items: center;
}

.encabezado div {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.section {
  display: flex;
  height: 65vh;
  flex-flow: column nowrap;
  padding: 0.2em;
}

.footer {
  display: flex;
  width: 100%;
  height: 10vh;
  background-color: black;
  color: white;
  bottom: 0;
  position: fixed;
  justify-content: center;
  align-items: center;
}

.section .contenedor {
  display: flex;
}

.section .formu {
  display: flex;
  flex-flow: column nowrap;
  width: 40%;
  padding: 0.1em;
}

.section .formu label {
  width: 90%;
  font-weight: bold;
  display: inline-block;
}

.section .formu input {
  width: 61%;
  height: 25px;
  padding: 3px 10px;
  border: 1px solid black;
  border-radius: 3px;
  background-color: #f6f6f6;
  margin: 8px 0;
  display: inline-block;
}

.section .formu button {
  width: 64.5%;
  height: 30px;
  padding: 3px 10px;
  border: 1px solid black;
  border-radius: 3px;
  background-color: #f6f6f6;
  margin: 8px 0;
  display: inline-block;
  cursor: pointer;
}

.section .table {
  display: flex;
  width: 60%;
  flex-flow: column nowrap;
  padding: 0.1em;
}

.section .table tr {
  display: flex;
  justify-content: space-between;
  padding: 0.1em;
}

.section .table tbody tr td .botonBorrar {
  border-radius: 0.2em;
  background-color: blue;
  border: 5px solid transparent;
  color: white;
  font-size: 20px;
  padding: 0.3em;
  cursor: pointer;
}